// Tip 出入库单据查询
import request from '@/apis/request.wms'

// 出库主列表
export function getOutListApi(data) {
  return request({
    // url: '/statistics/wmsStockInOut/inPageList',
    url: '/statistics/wmsStockInOut/outPageList',
    method: 'post',
    data
  })
}

// 入库主列表
export function getInListApi(data) {
  return request({
    url: '/statistics/wmsStockInOut/inPageList',
    method: 'post',
    data
  })
}

// 出库导出
export function outExportApi(data) {
  return request({
    url: '/statistics/wmsStockInOut/outExport',
    method: 'get',
    params: { ...data },
    responseType: 'blob',
    timeout: 500000
  })
}

// 入库导出
export function inExportApi(data) {
  return request({
    url: '/statistics/wmsStockInOut/inExport',
    method: 'get',
    params: { ...data },
    responseType: 'blob',
    timeout: 500000
  })
}
