<template>
  <div class="app-container">
    <div class="clearfix top-filter">
      <el-form ref="filterItem" :model="query" inline>
        <el-form-item prop="materialNo" class="fl">
          <el-input v-model.trim="query.materialNo" clearable placeholder="物料编号" class="filter-item" style="width: 178px;" @keyup.enter.native="searchInit" />
        </el-form-item>
        <el-form-item prop="billNumber" class="fl">
          <el-input v-model.trim="query.billNumber" clearable placeholder="单据编号" class="filter-item" style="width: 178px;" />
        </el-form-item>
        <el-button
          class="filter-item"
          type="success"
          icon="el-icon-search"
          @click="searchInit"
        >搜索</el-button>
        <el-button
          class="filter-item"
          type="warning"
          icon="el-icon-refresh-left"
          @click="resetForm('filterItem')"
        >重置</el-button>
      </el-form>
    </div>
    <el-row>
      <el-button
        class="filter-item"
        type="primary"
        icon="el-icon-setting"
        @click="setTable"
      >设置</el-button>
      <!-- <el-button
        class="filter-item"
        type="primary"
        icon="el-icon-plus"
        @click="handleExport"
      >导出</el-button> -->
      <el-button
        class="filter-item"
        type="primary"
        icon="el-icon-plus"
        @click="inExport"
      >导出</el-button>
    </el-row>
    <el-row>
      <!-- 列表 -->
      <table-com
        :ref="refTableName"
        :key="baseTableKey"
        :ref-name="refName"
        :loading="loading"
        :index="true"
        :selection="false"
        :table-column-sort="true"
        :form-ident="formIdent"
        :table-data="tableData"
        :column-list="columnList"
        :refresh-key="refreshKey"
        :expand-list="expandList"
        :expand-type="expandType"
        :show-operate="false"
        @rowClick="handleRowClick"
        @refresh="searchInit"
      >
        <template #expand-table="{scope}">
          <el-table :data="scope.row.materialList" border stripe class="expand-table">
            <el-table-column type="index" label="序号" fixed="left" align="center" />
            <el-table-column v-for="(c, i) in expandList1" :key="i" :prop="c.prop" :label="c.label" :align="c.align" :width="c.width" />
          </el-table>
        </template>
      </table-com>
    </el-row>
    <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" :page-sizes="[50, 100, 150, 500]" @pagination="init" />
  </div>
</template>
<script>
import tableHeadSet from '@/mixins/tableHeadSet'
import paginationPage from '@/mixins/paginationPage'
import { getInListApi as getListApi, inExportApi } from '@/apis/modules/analysis/outInboundOrder'
import { _debounce } from '@/utils'
import * as XLSX from 'xlsx'
import { saveAs } from 'file-saver'

export default {
  mixins: [tableHeadSet, paginationPage],
  data() {
    return {
      query: {
        materialNo: '',
        billNumber: ''
      },
      title: this.$route.meta.title,
      refTableName: 'baseTableRef',
      refName: 'table',
      baseTableKey: 1,
      loading: false,
      tableData: [],
      columnList: [],
      columnSets: [],
      expandType: 'table',
      formThead: [
        // { label: '单据类型', prop: 'type_dictText' },
        // { label: '物料凭证行项目', prop: 'type_dictText' },
        // { label: '物料凭证', prop: 'type_dictText' },
        // { label: '物料编号', prop: 'type_dictText' },
        // { label: '物料组', prop: 'type_dictText' },
        // { label: '物料描述', prop: 'type_dictText' },
        // { label: '单位', prop: 'type_dictText' },
        // { label: '数量', prop: 'type_dictText' },
        // { label: '金额', prop: 'type_dictText' },
        // { label: '币种', prop: 'type_dictText' },
        // { label: '发货日期', prop: 'type_dictText' },
        // { label: '抬头文本', prop: 'type_dictText' },
        // { label: '总账科目', prop: 'type_dictText' },
        // { label: '总账科目描述', prop: 'type_dictText' },
        // { label: '成本中心', prop: 'type_dictText' },
        // { label: '成本中心描述', prop: 'type_dictText' },
        // { label: '工厂', prop: 'type_dictText' },
        // { label: '库存地点', prop: 'type_dictText' },
        // { label: '评估类型', prop: 'type_dictText' },
        // { label: '资产', prop: 'type_dictText' },
        // { label: '资产描述', prop: 'type_dictText' },
        // { label: 'WBS元素', prop: 'type_dictText' },
        // { label: 'WBS元素描述', prop: 'type_dictText' },
        // { label: '移动类型', prop: 'type_dictText' },
        // { label: '订单', prop: 'type_dictText' },
        // { label: '订单描述', prop: 'type_dictText' },
        // { label: '备注', prop: 'type_dictText' },
        // { label: '收货方', prop: 'type_dictText' },
        // { label: '网络', prop: 'type_dictText' },
        // { label: '客户', prop: 'type_dictText' },
        // { label: '移入库存地点', prop: 'type_dictText' }
      ],
      expandList1: [
        {
          module: 'word',
          label: '物料编号',
          prop: 'materialCode',
          align: 'center',
          headAlign: 'center',
          sortable: false,
          width: '',
          minWidth: null,
          fixed: false,
          resizable: true,
          formatter: null
        },
        {
          module: 'word',
          label: '物料名称',
          prop: 'materialName',
          align: 'center',
          headAlign: 'center',
          sortable: false,
          width: '',
          minWidth: null,
          fixed: false,
          resizable: true,
          formatter: null
        },
        // {
        //   module: 'word',
        //   label: '条码号',
        //   prop: 'barcodeNumber',
        //   align: 'center',
        //   headAlign: 'center',
        //   sortable: false,
        //   width: '',
        //   minWidth: null,
        //   fixed: false,
        //   resizable: true,
        //   formatter: null
        // },
        {
          module: 'word',
          label: '评估类型',
          prop: 'materialAssessType_dictText',
          align: 'center',
          headAlign: 'center',
          sortable: false,
          width: '',
          minWidth: null,
          fixed: false,
          resizable: true,
          formatter: null
        },
        {
          module: 'word',
          label: '入库数量',
          prop: 'num',
          align: 'center',
          headAlign: 'center',
          sortable: false,
          width: '',
          minWidth: null,
          fixed: false,
          resizable: true,
          formatter: null
        },
        {
          module: 'word',
          label: '计量单位',
          prop: 'materialUnit',
          align: 'center',
          headAlign: 'center',
          sortable: false,
          width: '',
          minWidth: null,
          fixed: false,
          resizable: true,
          formatter: null
        },
        {
          module: 'word',
          label: '金额',
          prop: 'amount',
          align: 'center',
          headAlign: 'center',
          sortable: false,
          width: '',
          minWidth: null,
          fixed: false,
          resizable: true,
          formatter: null
        },
        {
          module: 'word',
          label: '库存地点',
          prop: 'warehouseStorageName',
          align: 'center',
          headAlign: 'center',
          sortable: false,
          width: '',
          minWidth: null,
          fixed: false,
          resizable: true,
          formatter: null
        }
      ]
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    init() {
      this.loading = true
      return new Promise((resolve, reject) => {
        getListApi({
          page: {
            ...this.pageInfo
          },
          ...this.query
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { formIdent, formHeadSets, pageData } = data
            this.formIdent = formIdent
            this.columnList = this.initTableHeadMixins(formHeadSets)
            this.$nextTick(() => {
              this.expandList = [{}]
            })
            const { list, ...pageInfo } = pageData
            this.initPage(pageInfo)
            this.tableData = list
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    download(result, name) {
      const filename = name
      const blob = new Blob([result])
      const url = window.URL.createObjectURL(blob)
      if (window.navigator.msSaveBlob) { // IE
        try {
          window.navigator.msSaveBlob(blob, filename)
        } catch (e) {
          console.log(e)
        }
      } else { // 非IE
        // window.open(url)
        const link = document.createElement('a')
        link.style.display = 'none'
        link.href = url
        link.setAttribute('download', filename)
        document.body.appendChild(link)
        link.click()
      }
      URL.revokeObjectURL(url) // 释放内存
    },
    async inExport() {
      // String materialNo  物料号，
      // String billNumber 出库单号
      try {
        const exp = await inExportApi(this.query)
        this.exportLoading = false
        this.download(exp, '入库单导出.xlsx')
      } catch (e) {
        console.log(e)
        this.$notify.error({
          title: '错误',
          message: '导出错误，请重试'
        })
      }
    },
    setTable() {
      this.$refs[this.refTableName].sortDialogVisible = true
    },
    handleRowClick({ row, column, cell, event }) {
      console.log(row)
      if (column.label === '操作') {
        return false
      }
      this.rightObj = { ...row }
    },
    handleExport: _debounce(function() {
      const elTable = this.$refs[this.refTableName].$refs[this.refName]
      // 因为element-ui的表格的fixed属性导致多出一个table，会下载重复内容，这里删除掉
      if (elTable.$el.querySelector('.el-table__fixed')) {
        elTable.$el.removeChild(elTable.$el.querySelector('.el-table__fixed'))
      }
      if (elTable.$el.querySelector('.el-table__fixed-right')) {
        elTable.$el.removeChild(elTable.$el.querySelector('.el-table__fixed-right'))
      }
      const wb = XLSX.utils.table_to_book(elTable.$el)
      const wbout = XLSX.write(wb, { bookType: 'xlsx', bookSST: true, type: 'array' })
      try {
        // 导出Excel
        const time = new Date().toLocaleString().replace(/\//g, '').replace(/:/g, '').replace(' ', '')
        saveAs(new Blob([wbout], { type: 'application/octet-stream' }), `入库单_${time}.xlsx`)
        this.baseTableKey++ // 更新表格组件，防止操作列消失
      } catch (e) {
        if (typeof console !== 'undefined') console.error(e, wbout)
      }
      return wbout
    }, 800, false),
    refresh() {
      // 更新分页
      this.init('clean')
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    }
  }
}
</script>

<style lang="scss" scoped>
</style>
